<template>
  <div class="createPost-container">
    <div class="createPost-main-container">
      <el-row>
        <el-col :span="24">
          <div class="postInfo-container">
            <el-row>
              <el-col :span="10">
                <el-date-picker
                  v-model="postForm.createTime"
                  type="date"
                  format="yyyy-MM-dd"
                  placeholder="选择日期时间"
                />
              </el-col>
            </el-row>
          </div>
        </el-col>
      </el-row>
      <div class="editor-container">
        <Tinymce ref="editor" :height="400" v-model="postForm.content"/>

        <el-button @click="submitForm" type="primary">发布</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import Tinymce from "@/components/Tinymce";
import { mapGetters } from "vuex";
import { Project } from "@/api/project";

export default {
  name: "createMetting",
  components: { Tinymce },
  data() {
    return {
      postForm: {
        createTime: new Date( // 当天23:59
          new Date(new Date().toLocaleDateString()).getTime()
        ).getTime(),
        content: `<p>客户端：</p>\n<p>1.&nbsp;</p>\n<p>服务端：</p>\n<p>1.&nbsp;</p>\n<p>测试：</p>\n<p>1.&nbsp;</p>\n<p>前端：</p>\n<p>1.&nbsp;</p>\n<p>设计：</p>\n<p>1.&nbsp;</p>\n<p>产品：</p>\n<p>1.</p>`
      }
    };
  },
  computed: {
    ...mapGetters(["name", "userId"])
  },
  methods: {
    submitForm() {
      if (!this.postForm.createTime) {
        this.$message.warning("请填写日期");
        return false;
      }
      if (!this.postForm.content.trim()) {
        this.$message.warning("内容不能为空");
        return false;
      }

      this.$confirm("确定发布?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Project.createMetting({
            ...this.postForm,
            author: this.name,
            userId: this.userId,
          }).then(res => {
            this.$message({
              type: "success",
              message: "发布成功"
            });
            this.$router.push("/project/mettingList");
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消"
          });
        });
    }
  }
};
</script>

